.toDolist-button-group {
  float: right;
  margin: 2px;
  padding-left: 5px;
  border-left: 1px solid rgb(122, 118, 118, 0.3);

}

.list-group-item{
  opacity: 0.94;
  overflow: hidden;
  /* margin-bottom: 20px; */
  padding: 5px;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.mark-dell-element-button {
margin-left: 5px;   
}

.icon-from-mark-dell-button {
  font-size: 24px;
  color:rgb(242, 242, 242);
}

:root {
--margin-offset: 100px; 
}

.animate-todo-element {
/* Safari 4.0 - 8.0 */
-webkit-animation-name: animationTodo;
-webkit-animation-duration: 0.7s;
-webkit-animation-timing-function: linear;
-webkit-animation-fill-mode: forwards;
/* Standard syntax */
animation-name: animationTodo;
animation-duration: 0.7s;
animation-timing-function: linear;
animation-fill-mode: forwards;
/* transform: scaleX(0.02)  translateX(500px); */

/* background-color: red; */
/* transition: all 1s ease-in-out; */
}

@-webkit-keyframes animationTodo {
  0%   {}
  40%  {color: red;background-color:red; -webkit-transform: scaleX(0.01) ; transform: scaleX(0.01) ; margin-left: var(--margin-offset)}
  100% {-webkit-transform:  scaleX(0.01) translateY(-900px) ;transform:  scaleX(0.01) translateY(-900px) ;margin-left: var(--margin-offset)}
}

/* Standard syntax */
@keyframes animationTodo {
0%   {}
40%  {color: red;background-color:red; -webkit-transform: scaleX(0.01) ; transform: scaleX(0.01) ; margin-left: var(--margin-offset)}
100% {-webkit-transform:  scaleX(0.01) translateY(-900px) ;transform:  scaleX(0.01) translateY(-900px) ;margin-left: var(--margin-offset)}
}

.play-add-todo-animation {
/* Safari 4.0 - 8.0 */
-webkit-animation-name: addTodoAnimation;
-webkit-animation-duration: 0.7s;
-webkit-animation-timing-function: linear;
-webkit-animation-fill-mode: forwards;
-webkit-animation-direction: reverse;
        animation-direction: reverse;
/* Standard syntax */
animation-name: addTodoAnimation;
animation-duration: 0.7s;
animation-timing-function: linear;
animation-fill-mode: forwards;
animation-direction: reverse;
/* transform: scaleX(0.02)  translateX(500px); */

/* background-color: red; */
/* transition: all 1s ease-in-out; */
  }

  @-webkit-keyframes addTodoAnimation {
    0%   {}
    40%  {color: #37ac51;background-color:#37ac51; -webkit-transform: scaleX(0.01) ; transform: scaleX(0.01) ; margin-left: var(--margin-offset)}
    100% {-webkit-transform:  scaleX(0.01) translateY(-900px) ;transform:  scaleX(0.01) translateY(-900px) ;margin-left: var(--margin-offset)}
  }
  
  /* Standard syntax */
  @keyframes addTodoAnimation {
  0%   {}
  40%  {color: #37ac51;background-color:#37ac51; -webkit-transform: scaleX(0.01) ; transform: scaleX(0.01) ; margin-left: var(--margin-offset)}
  100% {-webkit-transform:  scaleX(0.01) translateY(-900px) ;transform:  scaleX(0.01) translateY(-900px) ;margin-left: var(--margin-offset)}
  }


